
import * as Styled from './styles';
import { Heading } from '../Heading';
import LogoLink from 'next/LogoLink';


export type HeaderProps = {
  blogName: string;
  description:string;
  logo: string;
  showText?:boolean;
};

export const Header = ({
  blogName,
  description,
  logo,
  showText = false,

}: HeaderProps) => {

    return (
        <Styled.Wrapper>
          <LogoLink link="/" srcImg={logo} text={`${blogName} -${blogDescription}`}/>
          {showText && (
            <Styled.Content>
              <Heading size="small" as="h2" colorDark={true}>
                {blogName}
              </Heading>
            </Styled.Content>
          )}
        </Styled.Wrapper>
    );
  };
